<template>
  <div class="playlist">
    <div class="bg">
      <span @click="$router.push({ path: '/' })" class="fanhui"
        ><i class="fa fa-chevron-down"></i
      ></span>
      <div class="mask">
        <div class="mark-box"></div>
        <p class="red">歌单</p>
        <img :src="playlist?.coverImgUrl" alt="" />
        <span><span class="spanImg"></span> {{ math }}</span>
      </div>
      <div>
        <p>{{ playlist?.name }}</p>
        <div>
          <img :src="playlist?.creator?.avatarUrl" alt="" /><span>{{
            playlist?.creator?.nickname
          }}</span>
          <img :src="playlist?.creator?.avatarDetail?.identityIconUrl" alt="" />
        </div>
      </div>
      <div class="mark" :style="bg"></div>
    </div>
    <h3 class="h3">歌单列表</h3>
    <div class="box">
      <div
        v-for="(item, index) in playlist?.tracks"
        :key="item.id"
        @click="$emit('playsong', item), $emit('musiclist', playlist?.tracks)"
        :class="{act:item.id==songId}"
      >
        <div>
          <span> {{ index + 1 }}</span>
        </div>
        <div class="gray">
          <div>
            <p>{{ item.name }}</p>
            <p>{{ item.ar[0].name }}-{{ item.al.name }}</p>
          </div>
          <div v-if="item.id !== songId" class="newsong-play"></div>
          <div v-else class="newsong-play active" :class="{ playing: playing }">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
        <!-- <li>{{ item.name }}</li> -->
      </div>
    </div>
    <div class="comments" v-if="comments.hotComments==[]">
      <h3 class="h3">精彩评论</h3>
      <div
        v-for="item in comments.hotComments"
        :key="item.commentId"
        class="user"
      >
        <div>
          <img
            :src="`${
              item.user?.avatarUrl ? item.user.avatarUrl : ''
            }?imageView=1&type=webp&thumbnail=30x0`"
            alt=""
          />
        </div>
        <div class="userbox">
          <div class="username">
            <p>
              <span>{{ item.user.nickname }}</span
              ><img
                :src="`${
                  item.user?.vipRights?.associator
                    ? item.user.vipRights.associator.iconUrl
                    : ''
                }?imageView=1&type=webp&thumbnail=30x0`"
                alt=""
              />
            </p>
            <span class="give" @click="item.likedCount++"
              >{{ item.likedCount }}
              <i class="fa fa-thumbs-o-up" aria-hidden="true"></i
            ></span>
          </div>
          <p class="time">{{ item.timeStr }}</p>
          <p>{{ item.content }}</p>
        </div>
      </div>
    </div>
    <div v-else class="comments">
      <h3 class="h3">最新评论</h3>
      <div
        v-for="item in comments.comments"
        :key="item.commentId"
        class="user"
      >
        <div>
          <img
            :src="`${
              item.user?.avatarUrl ? item.user.avatarUrl : ''
            }?imageView=1&type=webp&thumbnail=30x0`"
            alt=""
          />
        </div>
        <div class="userbox">
          <div class="username">
            <p>
              <span>{{ item.user.nickname }}</span
              ><img
                :src="`${
                  item.user?.vipRights?.associator
                    ? item.user.vipRights.associator.iconUrl
                    : ''
                }?imageView=1&type=webp&thumbnail=30x0`"
                alt=""
              />
            </p>
            <span class="give" @click="item.likedCount++"
              >{{ item.likedCount }}
              <i class="fa fa-thumbs-o-up" aria-hidden="true"></i
            ></span>
          </div>
          <p class="time">{{ item.timeStr }}</p>
          <p>{{ item.content }}</p>
          <!-- <p>{{ item.content }}</p> -->
        </div>
      </div>
    </div>
    <div class="all">查看全部{{ comments.total }}评论></div>
  </div>
</template>

<script>
export default {
  props:{
    songId:Number,
    playing:Boolean
  },
  data: function () {
    return {
      playlist: {},
      bg: {},
      comments: [],
    };
  },
  created() {},
  computed: {
    math() {
      if (this.playlist) {
        return this.playlist.playCount > 100000000
          ? `${(this.playlist.playCount / 100000000).toFixed(1)}亿`
          : this.playlist.playCount > 10000
          ? `${(this.playlist.playCount / 10000).toFixed(1)}万`
          : this.playlist.playCount;
      } else {
        return null;
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    console.log(to.query.id);
    next((vm) => {
      vm.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: {
            id: to.query.id,
          },
        })
        .then((res) => {
          console.log(res);
          vm.playlist = res.data.playlist;
          vm.bg = {
            backgroundImage: `url(${vm.playlist.coverImgUrl})`,
          };
        });
      vm.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: {
            id: to.query.id,
          },
        })
        .then((res) => {
          console.log(res);
          vm.comments = res.data;
        });
        
    });
  },
  beforeRouteUpdate(to, from, next) {
    console.log(to);
    // console.log(this.$router.history.current.query.id);
    // this.axios.get("https://apis.netstart.cn/music/playlist/detail",
    // {params:{
    //     id:this.$router.history.current.query.id
    // }}).then(res=>{
    //     console.log(res);
    // })
    next();
  },
};
</script>

<style lang="less" scoped>
.act{

  color: red;
}
.playlist {
  margin-bottom: 60rem;
}
.bg {
  display: flex;
  align-items: center;
  width: 100%;
  height: 200rem;
  background-color: rgba(0, 0, 0, 0.25);
  overflow: hidden;
  position: relative;
  .fanhui {
    position: absolute;
    left: 10rem;
    font-size: 16rem;
    top: 0;
    color: white;
  }
  p {
    font-size: 17rem;
    z-index: 999;
  }
  div:nth-of-type(2) {
    width: 60%;
    color: white;
    height: 126rem;
  }
  div.mask {
    width: 126rem;
    height: 126rem;
    position: relative;
    margin: 0 20rem;
    box-sizing: border-box;
    .mark-box {
      width: 100%;
      height: 30rem;
      // background-color: rgba(0, 0, 0, 0.082);
      // color: transparent;
      z-index: 999;
      background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.2),
        rgba(0, 0, 0, 0)
      );
      position: absolute;
      left: 0;
      top: 0;
    }
    .red {
      color: white;
      font-size: 12rem;
      text-align: center;
      top: 10rem;
      width: 40rem;
      height: 17rem;
      z-index: 999;
      background-color: rgba(217, 48, 48, 0.8);
      border-radius: 0 17rem 17rem 0;
    }
    p {
      position: absolute;
      left: 0;
    }
    img {
      z-index: 999;
      width: 126rem;
      height: 126rem;
    }
    > span {
      display: inline;
      right: 5rem;
      top: 5rem;
      position: absolute;
      display: flex;
      align-items: center;
      color: white;
      .spanImg {
        display: inline-block;
        width: 12rem;
        height: 12rem;
        background-size: cover;
        margin: 0 2rem;
        background-repeat: no-repeat;
        // background-color: aquamarine;
        background-image: url("data:image/svg+xml;base64,");
      }
    }
  }
  div > div {
    // margin: 5rem 0;
    display: flex;
    align-items: center;
    z-index: 999;
    // width: 32rem;
    height: 32rem;
    position: relative;
    img {
      width: 32rem;
      border-radius: 50%;
    }
    img:nth-of-type(2) {
      position: absolute;
      left: 25rem;
      bottom: 0;
      width: 12rem;
      height: 12rem;
    }
    span {
      margin: 0 10rem;
      color: hsla(0, 0%, 100%, 0.7);
    }
  }
  .mark {
    width: 100%;
    filter: blur(20rem);
    height: 100%;
    position: absolute;
    transform: scale(1.2);
    // background-position: 20%;
    background-position: center;
    background-repeat: no-repeat;

    left: 0;

    z-index: -1;
  }
}
.h3 {
  margin-left: 5rem;
  color: gray;
  font-size: 16rem;
  background-color: #eeeff0;
}
.box {
  > div {
    display: flex;
    align-items: center;
    font-size: 19rem;
    span {
      font-weight: 300;
      color: gray;
      margin: 0 10rem;
    }
  }
  .gray {
    width: 100%;
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    padding: 6rem 5rem 10rem 0;
    // margin: 5rem 0;
    border-bottom: 1rem solid rgba(128, 128, 128, 0.082);
    div > p:nth-of-type(2) {
      font-size: 12rem;
      color: gray;
    }
    > div:nth-of-type(1) {
      width: 80%;
      overflow: hidden;
    }
  }
  .bg2 {
    margin: 0;
    display: inline-block;
    width: 24rem;
    height: 8rem;
    color: white;
    background-size: 740%;
    background-repeat: no-repeat;
    background-position: 2rem 0rem;
    background-image: url("../assets/1.png");
  }
  .bofang {
    width: 22rem;
    height: 22rem;
    background-size: 750%;
    background-image: url("../assets/1.png");
    background-position: -24rem 0;
  }
}
.comments {
  .user {
    display: flex;

    > div {
      > img {
        width: 30rem;
        height: 30rem;
        border-radius: 50%;
        margin: 10rem;
      }
    }
    .userbox {
      // margin: 5rem;
      padding: 10rem;
      // height: 123rem;
      width: 100%;
      border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
      font-size: 15rem;
      .time {
        color: #999;
        font-size: 12rem;
      }
    }
    .username {
      color: #666;
      display: flex;
      justify-content: space-between;
      align-items: center;
      p {
        display: flex;
        align-items: center;
      }
      .give {
        color: #999;
      }
    }
  }
}
.all {
  width: 100%;
  padding: 18rem;
  text-align: center;
  color: #999;
  border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
  opacity: 0.5;
}
.add {
  width: 100%;
  text-align: center;
  padding: 10rem 20rem;
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: white;
  div {
    width: 100%;
    height: 40rem;
    // margin: 10rem;
    font-size: 15rem;
    line-height: 40rem;
    border-radius: 32rem;
    background-color: rgb(255, 58, 58);
    color: white;
  }
}
 @keyframes playingrunning {
    from {
      transform: rotateX(15deg);
    }
    to {
      transform: rotateX(60deg);
    }
  }
  .newsong-play {
    background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 10rem;
    top: 50%;
    transform: translateY(-50%);
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        flex-shrink: 0;
        margin: 0;
        height: 100%;
        background: red;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
</style>