<template>
  <div class="home">
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <h3>编辑推荐</h3>
    <HomeItem v-for="item in context" :key="item.id" :item="item" />
    <h3>新歌推荐</h3>
    <!-- @yinyue="yinyue" -->
    <HomeName
      v-for="item in newsong"
      :key="item.id"
      :item="item"
      @playsong="$emit('playsong', $event)"
      :playing="playing"
      :songId="songId"
      @click="$emit('musiclist', newsong)"
      @click.native="$emit('musiclist',newsong)"
    />
    <HomeFoot />
    <div class="music-mark"></div>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeItem from "@/components/HomeItem.vue";
import HomeName from "@/components/HomeName.vue";
import HomeFoot from "@/components/HomeFoot.vue";

export default {
  name: "HomeView",
  props:{
playing:Boolean,
songId:Number,
  },
  components: {
    HomeItem,
    HomeName,
    HomeFoot,
  },
  data: function () {
    return {
      context: [],
      newsong: [],
    };
  },
  methods: {
    // yinyue(id){
    //   this.axios.get("https://apis.netstart.cn/music/song/url",{params:{
    //     id
    //   }}).then(res=>{
    //     console.log(res);
    //   })
    // }
  },
  created() {
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized", {
        params: { limit: 6 },
      }),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
    ]).then(([a, b]) => {
      this.context = a.data.result;
      this.newsong = b.data.result;
      // console.log(a.data.result,b);
    });
  },
};
</script>
