<template>
  <!-- 歌词 -->
  <div class="playpage">
    <div class="mm"></div>
    <div
      class="markbg"
      :style="{
        backgroundImage: `url(${song.picUrl ? song.picUrl : song.al.picUrl})`,
      }"
    ></div>
    <div class="spanbox">
      <span @click="$emit('showmusic', 'false')"
        ><i class="fa fa-chevron-down"></i
      ></span>
      <div>
        <h1>{{ song.name }}</h1>
      </div>
      <span class="fa fa-share-alt"></span>
    </div>
    <div v-if="!ci" class="ge" @click="ci = !ci">
      <div class="musicbg">
        <div
          class="gang"
          :style="{ transform: playing ? 'rotate(-10deg)' : 'rotate(-30deg)' }"
        ></div>
        <img
          :class="{ playing: playing }"
          :src="
            song
              ? `${
                  song.picUrl ? song.picUrl : song.al.picUrl
                }?imageView=1&type=webp&thumbnail=270x0`
              : ''
          "
          alt=""
        />
      </div>
      <!-- <ul >
        <li
          :class="{ act: i == index }"
          :style="{ transform: `translateY(${-21 * index}rem)` }"
          v-for="(item, i) in lyr"
          :key="i"
        >
          {{ item?.text }}
        </li>
      </ul> -->
    </div>
    <div class="cibox" v-else @click="ci = !ci">
      <p
        v-for="(item, i) in lyr"
        :key="i"
        ref="p"
        :class="{ act: i == index }"
        :style="{ transform: `translateY(${-31 * index + 200}rem)` }"
      >
        {{ item.text || "..." }}
      </p>
    </div>
    <div class="bar">
      <span>{{ mathct }}</span>
      <div>
        <label for="">
          <input
            type="range"
            :value="currentTime"
            min="0"
            @input="
              (draging = true), $emit('currentvalue', $event.target.value)
            "
            :max="duration"
            @change="
              (draging = false), $emit('currentvalue', $event.target.value)
            "
          />
          <p
            :style="{
              left: pvalue,
            }"
          ></p>
        </label>
        <div :style="{ width: pvalue }"></div>
      </div>
      <span>{{ mathdt }}</span>
    </div>
    <!-- <span class="order">{{ modelci }}</span> -->
    <div class="foot-btn">
      <button @click="$emit('musicModel')"><i :class="model"></i></button>
      <button @click="$emit('musictoggle', 'up')">
        <i class="fa fa-fast-backward"></i>
      </button>
      <div class="btn">
        <button v-if="!playing" @click.stop="$emit('song-play')">▶</button>
        <button v-else @click.stop="$emit('song-pause')">||</button>
        <canvas width="50" height="50" ref="canvas"></canvas>
      </div>
      <button @click="$emit('musictoggle', 'next')">
        <i class="fa fa-fast-forward"></i>
      </button>
      <!-- <button @click="$emit('toggle-showPlayList')">列表</button> -->
      <button @click.stop="$emit('toggle-showPlayList')">
        <i v-if="listShow" class="fa fa-close"></i
        ><i v-else class="fa fa-list-ul"></i>
      </button>
      <!-- <button>收藏</button> -->
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    currentTime: Number,
    duration: Number,
    playing: Boolean,
    model: String,
    listShow: Boolean,
    modelci: String,
  },
  data: function () {
    return {
      ci: false,
      lyric: null,
      index: null,
      draging: false,
    };
  },
  computed: {
    mathct() {
      // return Math.round((this.currentTime / 60) * 100) / 100;
      return (
        String(Math.floor(this.currentTime / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.currentTime % 60)).padStart(2, "0")
      );
    },
    mathdt() {
      return (
        String(Math.floor(this.duration / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.duration % 60)).padStart(2, "0")
      );
    },
    pvalue() {
      return (this.currentTime / this.duration) * 100 + "%";
    },
    proportion() {
      return this.currentTime / this.duration;
    },
    lyr() {
      return this.lyric
        ?.filter((o) => o)
        .map((o) => {
          try {
            var { groups } = o?.match(
              /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
            );
            return {
              time: groups.mm * 60 + groups.ss * 1,
              text: groups.text.trim(""),
            };
          } catch {
            console.log("123");
            console.log(
              o?.match(/\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/)
            );
            return "";
          }
        })
        .filter((e) => e != "");
    },

    // act(){
    //     return
    // }
  },

  methods: {
    // act(lyric, params) {
    //   lyric.forEach((e) => {
    //     console.log(e);
    //   });
    //   // console.log(params);
    // },
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/music/lyric", {
        params: {
          id: this.song.id,
        },
      })
      .then((res) => {
        // console.log(res.data.lrc.lyric);
        this.lyric = res.data.lrc.lyric.split("\n");
        // console.log(res, this.song.id);
      });
  },
  watch: {
    song(id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: {
            id: id.id,
          },
        })
        .then((res) => {
          // console.log(res.data.lrc.lyric);
          this.lyric = res.data.lrc.lyric.split("\n");
          // console.log(res, this.song.id);
        });
    },
    currentTime(n) {
      this.index = this.lyr?.findIndex((o) => o.time > n);
    },
    proportion(n) {
      let canvas = this.$refs.canvas;
      let context = canvas.getContext("2d");
      context.clearRect(0, 0, 50, 50);
      context.beginPath();
      // context.arc(25,25,24,0,Math.PI*2)
      context.arc(25, 25, 26, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      context.stroke();
      context.strokeStyle = "red";
      context.lineWidth = 4;

      // console.log(n);
    },
  },
};
</script>

<style lang="less" scoped>
.playpage {
  .order {
    position: absolute;
    left: 45%;
    bottom: 15%;
  }
  .mm {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
    background-color: rgba(128, 128, 128, 0.349);
  }
  background-size: cover;
  background-position: center top;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  z-index: 9999;

  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  .markbg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    background-size: cover;
    transform: scale(1.5);
    filter: blur(40rem);
    z-index: -1;
  }
  .spanbox {
    width: 90%;
    display: flex;
    align-items: center;
    font-size: 16rem;
    // position: relative;
    z-index: 999;
    display: flex;
    justify-content: space-between;
    padding: 0 20rem;
    height: 50rem;
    align-items: center;
    h1{
      width:60%;
      margin: 0 auto;
      white-space: nowrap;
      overflow: hidden;
    }
    > div {
      width: 100%;
      text-align: center;
    }
  }
  .bar {
    // height: 1rem;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 20rem;
    z-index: 999;
    span {
      color: white;
    }
    > div {
      width: 100%;
      height: 1rem;
      background-color: #999;
      position: relative;
      margin: 0 10rem;
      input {
        position: absolute;
        left: 0;
        top: -7rem;
        width: 100%;
        opacity: 0;
      }
      > div {
        background-color: orangered;
        height: 1rem;
        position: absolute;
        left: 0;
        top: 0;
      }
      p {
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        background-color: #fff;
        box-shadow: 0 0 3rem 0 black;
        position: absolute;
        left: 0;
        top: -2rem;
      }
    }
  }
  .ge {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    padding: 0 20rem;
    .musicbg {
      width: 300px;
      height: 300px;
      // background-color: black;
      background-image: url(https://p5.music.126.net/obj/wo3DlcOGw6DClTvDisK1/4602256867/5855/63fb/325b/d7e4e3a244701ee85fecb5d4f6b5bd57.png?imageView=&quality=75&thumbnail=200y200);
      background-repeat: no-repeat;
      background-size: cover;
      border-radius: 50%;
      // animation: move 8s linear infinite;
      display: flex;
      align-items: center;
      position: relative;
      justify-content: center;
      .gang {
        position: absolute;
        right: 80rem;
        transform-origin: 21.5% 11.5%;
        // transform-origin:top left;
        transition: all 0.3s;
        transform: scale(0.5);
        top: -60rem;
        z-index: 9999;
        width: 100rem;
        height: 140rem;
        background: url(//s3.music.126.net/mobile-new/img/needle-ab.png?4b81da043356110081e5ccaed2e6f2ad=)
          no-repeat;
        background-size: 80%;
        background-position: center;
      }
      // background:url(//s3.music.126.net/mobile-new/img/disc.png?d3bdd1080a72129346aa0b4b4964b75f=) no-repeat ;
    }
  }
  @keyframes move {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  img {
    width: 200rem;
    height: 200rem;
    border-radius: 50%;
    animation: move 8s linear infinite;
    animation-play-state: paused;
    &.playing {
      animation-play-state: running;
    }
  }
  ul {
    //   width: 300rem;
    width: 100%;
    height: 42rem;
    overflow: hidden;
    text-align: center;
    li {
      padding: 0 20rem;
      font-size: 16rem;
      height: 21rem;
      &.act {
        color: red;
      }
    }
  }
  .cibox {
    overflow: hidden;
    width: 100%;
    height: 400rem;
    text-align: center;
    h1 {
      font-size: 20rem;
    }
    p {
      font-size: 16rem;
      // padding: 0 30rem;
      // overflow: hidden;
      margin: 10rem 30rem;

      white-space: nowrap;
      overflow: hidden;
      // text-overflow: ellipsis;
      // position: relative;;
      height: 21rem;
      &.act {
        color: red;
      }
    }
  }
  .foot-btn {
    width: 100%;
    height: 50rem;
    display: flex;
    // align-items: center;
    justify-content: space-evenly;
    margin-bottom: 20rem;
    z-index: 999;
    button {
      width: 50rem;
      height: 100%;
      // background-color: aquamarine;
      background-color: transparent;
      color: white;
      font-size: 16rem;
      border: 0;
      outline: 0;
    }
    .btn {
      width: 50rem;
      height: 50rem;
      position: relative;
      button {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
        border: none;
        outline: none;
        background-color: transparent;
      }
      canvas {
        border-radius: 50%;
        // outline: 1rem solid gray;
        box-shadow: 0 0 3rem 0 gray;
      }
    }
  }
}
</style>