<template>
  <div
    class="home-card"
    @click="$router.push({ path: '/playlist', query: { id: item.id } })"
  >
    <img :src="item.picUrl" />
    <p class="item">{{ item.name }}</p>
    <p class="top">
      <span class="spanImg"></span><span>{{ math }}</span>
    </p>
    <div class="mark"></div>
  </div>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  computed: {
    math() {
      // return Math.round((this.item.playCount / 10000) * 10) / 10;
      if (this.item.playCount) {
        return this.item.playCount > 100000000
          ? `${(this.item.playCount / 100000000).toFixed(1)}亿`
          : this.item.playCount > 10000
          ? `${(this.item.playCount / 10000).toFixed(1)}万`
          :this.item.playCount
      } else {
        return null;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.home-card {
  margin: 5rem 0;
  width: 33%;
  position: relative;
  .mark {
    width: 100%;
    height: 40rem;
    // background-color: rgba(0, 0, 0, 0.082);
    // color: transparent;
    z-index: 99;
    background-image: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0)
    );
    position: absolute;
    left: 0;
    top: 0;
  }
  .item{
        display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6px 2px 0 6px;
  }
  .top {
    color: white;
    // box-shadow: 0 0 10rem 0 black;
    // text-shadow: 0 0 5rem black;
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
    margin: 5rem;
    z-index: 999;
  }
  .spanImg {
    display: inline-block;
    width: 12rem;
    height: 12rem;
    background-repeat: no-repeat;
    // background-color: aquamarine;
    background-image: url("data:image/svg+xml;base64,");
  }
}
</style>