<template>
<!-- -->
  <div class="player"  :class="{ act: playing }" v-show="song ">
    <div class="img-box" :class="{ act: playing }">
      <img
        :class="{ playing: playing }"
        :src="
          song
            ? `${
                song.picUrl ? song.picUrl : song.al.picUrl
              }?imageView=1&type=webp&thumbnail=50x0`
            : ''
        "
        alt=""
      />
    </div>
    <h3 class="musicname">{{ song?.name }}</h3>
    <p>{{ mathct }} / {{ mathdt }}</p>
    <!-- <input
      type="range"
      :max="duration"
      min="0"
      :value="currentTime"
      @change="$emit('currentvalue', $event.target.value)"
    /> -->
    <div class="btn">
      <button v-if="!playing" @click.stop="$emit('song-play')">▶</button>
      <button v-else @click.stop="$emit('song-pause')">||</button>
      <!-- <div v-if="playmark" class="playmark"></div> -->
      <canvas width="50" height="50" ref="canvas"></canvas>
    </div>
    <div @click.stop="$emit('toggle-showPlayList')" class="btn2">
      <i class="fa fa-list-ul"></i>
    </div>
  </div>
</template>

<script>
// let now=new Date()

//调用

export default {
  props: {
    song: Object,
    duration: Number,
    playing: Boolean,
    musicBool: Boolean,
    currentTime: Number,
    listShow:Boolean,
  },

  data: function () {
    return {};
  },
  computed: {
    mathct() {
      // return Math.round((this.currentTime / 60) * 100) / 100;
      return (
        String(Math.floor(this.currentTime / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.currentTime % 60)).padStart(2, "0")
      );
    },
    mathdt() {
      return (
        String(Math.floor(this.duration / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.duration % 60)).padStart(2, "0")
      );
    },
    proportion() {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    proportion(n) {
      let canvas = this.$refs.canvas;
      let context = canvas.getContext("2d");

      context.stroke();
      context.clearRect(0, 0, 50, 50);
      context.beginPath();
      // context.arc(25,25,24,0,Math.PI*2)
      context.arc(25, 25, 26, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      context.stroke();
      context.strokeStyle = "red";
      context.lineWidth = 4;

      // console.log(n);
    },
  },
};
</script>

<style lang="less" scoped>
.player {
  position: fixed;
  bottom: 0;
  z-index: 999;
  width: 100%;
  height: 60rem;
  background-color: rgb(43, 41, 41);
  color:white ;
  display: flex;
  align-items: center;
  justify-content: space-around;
  // position: relative;
  @keyframes move {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  .musicname{
    width: 100rem;
  }
  .img-box {
    width: 60rem;
    height: 60rem;
    // background-color: black;
    background-image: url('../assets/bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    margin-bottom: 25rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: move 8s linear infinite;
    animation-play-state: paused;
    &.act {
      animation-play-state: running;
    }
    img {
      width: 40rem;
      height: 40rem;
      border-radius: 50%;
    }
  }
  .btn2{
    font-size: 16rem;
  }
  .btn {
    width: 50rem;
    height: 50rem;
    position: relative;
    button {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      border: none;
      outline: none;
      color: white;
      background-color: transparent;
    }
    canvas {
      border-radius: 50%;
      // outline: 1rem solid gray;
      box-shadow: 0 0 3rem 0 gray;
    }
  }
}
</style>