<template>
  <div class="box">
    <div class="artistbg">
      <span @click="$router.back()" class="fanhui"
        ><i class="fa fa-chevron-down"></i
      ></span>
      <img
        :src="`${
          artist?.artist ? artist?.artist.picUrl : ''
        }?imageView=1&type=webp&thumbnail=375x0`"
        alt=""
      />
      <div></div>
    </div>
    <h1>歌手简历</h1>
    <div class="name">
      <p :class="{ hh:h&&!oo}" ref="p">{{ artist?.artist.briefDesc }}</p>
    </div>
    <p @click="oo = !oo" v-show="h">
      <span v-if="!oo" class="fa fa-angle-down">完整歌手详细</span>
      <span v-else class="fa fa-angle-up">收起</span>
    </p>
    <!-- <div v-for="item in artist?.hotSongs" :key="item.id">
        </div> -->
    <h1>热门单曲</h1>
    <div
      v-for="(item, index) in artist?.hotSongs"
      :key="index"
      @click="$emit('playsong', item), $emit('musiclist', artist?.hotSongs)"
      :class="{act:item.id==songId}"
    >
      <div>
        <span class="num"> {{ String(index + 1).padStart(2, "0") }}</span>
      </div>
      <div class="gray">
        <div>
          <p>{{ item.name }}</p>
          <p>
            <span class="bg2"></span>{{ item.ar[0].name }}-{{ item.al.name }}
          </p>
          <div v-if="item.id !== songId" class="newsong-play"></div>
          <div v-else class="newsong-play active" :class="{ playing: playing }">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // artist?id=31165848
  props: {
    songId: Number,
    playing: Boolean,
  },
  data: function () {
    return {
      artist: null,
      oo: false,
      h:false
    };
  },
  methods: {},
  mounted() {
    // this.h=this.$refs.p.offsetHeight>50
  },
  computed: {
    
  },

  beforeRouteEnter(to, from, next) {
    console.log(to.query.id);
    next((vm) => {
      vm.axios
        .get("https://apis.netstart.cn/music/artists", {
          params: {
            id: to.query.id,
          },
        })
        .then( (res) => {
          console.log(res);
          //   for (let i = 0; i <5; i++) {
          //     vm.artist.push(res.data.hotSongs[i])
          //   }
          setTimeout(()=>{
           vm.h=vm.$refs.p.offsetHeight>=50;
          },0)

          vm.artist = res.data;
        });
    });
  },
};
</script>

<style lang="less" scoped>
.act{
  color: red;
}
.box {
  .name {
    display: inline-block;

    margin: 10rem;
    // padding: 0 5rem;
    p {
      display: -webkit-box;
      overflow: hidden;
      -webkit-box-orient: vertical;
      // text-overflow: ellipsis;
      font-size: 12rem;
      color: #666;
      &.hh {
        -webkit-line-clamp: 3;
        height: 50rem;
      }
    }
  }
  > p {
    text-align: center;
    color: #666;
  }
  h1 {
    font-size: 16rem;
    margin: 10rem;
    font-weight: bold;
  }
  .artistbg {
    width: 100%;
    // height: 310rem;
    // position: relative;
    img {
      width: 100%;
      // height: 100%;
    }
    .fanhui {
      position: absolute;
      left: 10rem;
      font-size: 16rem;
      top: 0;
      color: white;
    }
  }
  > div {
    display: flex;
    align-items: center;
    font-size: 19rem;
    .num {
      font-weight: 300;
      color: gray;
      margin: 0 10rem;
    }
  }
  > div:nth-child(1) {
    span {
      color: red;
    }
  }
  > div:nth-child(2) {
    span {
      color: red;
    }
  }
  > div:nth-child(3) {
    span {
      color: red;
    }
  }
  .gray {
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
    align-items: center;
    padding: 6rem 5rem 10rem 0;
    > div {
      width: 100%;
      position: relative;
    }
    // margin: 5rem 0;
    border-bottom: 1rem solid rgba(128, 128, 128, 0.082);
    div > p:nth-of-type(2) {
      font-size: 12rem;
      color: gray;
    }
  }
  .bg2 {
    margin: 0;
    display: inline-block;
    width: 24rem;
    height: 8rem;
    color: white;
    background-size: 740%;
    background-repeat: no-repeat;
    background-position: 2rem 0rem;
    background-image: url("../assets/1.png");
  }
  @keyframes playingrunning {
    from {
      transform: rotateX(15deg);
    }
    to {
      transform: rotateX(60deg);
    }
  }
  .newsong-play {
    background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 10rem;
    top: 50%;
    transform: translateY(-50%);
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 100%;
        background: red;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>